body{font-family:'Open Sans',sans-serif;font-size:18px;color:#000;font-weight:400}a{text-decoration:none;-webkit-transition:.3s ease-in-out;-moz-transition:.3s ease-in-out;transition:.3s ease-in-out;cursor:pointer}header{position:relative;display:block;width:100%;color:#fff;background:#000;height:100px}header .logo{position:absolute;top:50%;left:15px;-webkit-transform:translate(50%);-moz-transform:translate(50%);transform:translate(50%)}header nav,header nav ul,header nav ul li{margin:0;padding:0}header nav{position:absolute;top:50%;right:15px;-webkit-transform:translate(-50%);-moz-transform:translate(-50%);transform:translate(-50%)}header nav ul li{list-style-type:none;display:inline-block;margin-right:15px}header nav ul li.last{margin:0}header nav ul li a{font-size:18px;line-height:normal;color:#fff}header nav ul li a:hover{font-size:18px;line-height:normal;color:#ff0004;text-decoration:none}